import React, { Component } from 'react';
import {  Card, CardTitle, CardText } from 'react-mdl';


class Projects extends Component {


  render() {
    return(
      <div className="projects-grid">
        
        <Card shadow={5} style={{minWidth: '450',margin: '20px', padding:'10px'}}>
          <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://xtnotes-1255646395.coshk.myqcloud.com/images/react-1.svg) center / cover'}} ></CardTitle>
            <CardText>
            <h5>ReactJS Project</h5>
            ReactJS, Bootstrap, JavaScript is used, to make structure for website ReactJS is used, Bootstrap for styling and java scripting for responding to request of user and its main aim was to act like a portfolio of a person and learned how to manage the components in react and learned how to do Bootstrap for styling.
            </CardText>
        </Card>
        
        <Card shadow={5} style={{minWidth: '450', margin: '20px', padding:'10px'}}>
          <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://img.icons8.com/color/2x/nodejs.png) center / cover'}} ></CardTitle>
            <CardText>
              <h5>NodeJS Project</h5>
              Node.js Technology is used to make different API’s that can be used in giving a response action behaviour from backend to the request got from front end and its main aim is to manage the data of the user and store all the transactions details and can check the current status of user account. Transactions like credit, transfer and debit are included, learned how to use Postman, MAMP, JSON library, MySQL, Middleware framework and connecting API with database.
            </CardText>
        </Card>

        <Card shadow={5} style={{minWidth: '450',margin: '20px', padding:'10px'}}>
          <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://img.icons8.com/color/2x/python.png) center / cover'}} ></CardTitle>
            <CardText>
            <h5>Python Project</h5>
            Python Programming is used as it has packages to make UI based Interface and its main aim was to give the information of a courier to the user and can track his courier, learned how to build UI based application using python language and how to import the images into the application and designing the interface attractive
            </CardText>
        </Card>

        <Card shadow={5} style={{minWidth: '450',margin: '20px', padding:'10px'}}>
          <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://img.icons8.com/color/2x/html-5.png) center / cover'}} ></CardTitle>
            <CardText>
            <h5>HTML/CSS/JS Project</h5>
            HTML, Java script, CSS. make structure for website HTML used, CSS for styling and java scripting for responding to request of user and its main aim was to give the information for the user like bank schemes, balance enquiry, creating new account, loans availability, money transfer, etc., learned how to respond to the request of user and designing the website more attractive and user friendly
            </CardText>
        </Card>

      </div>
    )
  }
}

export default Projects;